import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'biu-ngx-bulma-image-uploader',
  template: `
    <p>ngx-bulma-image-uploader works!</p>
    <button class="button is-primary" (click)="addData()">add</button>
  `,
  styles: [],
})
export class NgxBulmaImageUploaderComponent implements OnInit {
  constructor(private afStore: AngularFirestore) {}

  ngOnInit(): void {}

  async addData(): Promise<void> {
    await this.afStore.doc('some/data').set({
      name: 'hoge',
    });
  }
}
